
@import "../../scss/_global.scss";
.delivery-form {
  h3 {
    font-family: "Roboto";
    font-style: normal;
    font-weight: 700;
    font-size: 22px;
    line-height: 25px;
    color: $gray;
    margin-bottom: 30px;

    @media (max-width: 768px) {
      font-size: 20px;
      line-height: 23px;
      font-weight: 500;
      text-align: center;
      margin: -10px auto 10px;
    }
  }

  .button {
    @extend %button;
    margin: 40px auto 0;
    width: 263px;

    @media (max-width: 576px) {
      width: 145px;
      height: 45px;
      margin-top: 24px;
    }
  }
  .user-info {
    display: flex;
    gap: 14px;
    flex-direction: column;

    @media (max-width: 576px) {
      gap: 10px;
      max-width: 330px;
      margin: 0 auto;
    }
  }
}
