
@import "../../scss/_global.scss";

#delivery {
  @extend %indent-p;

  .delivery-wrapper {
    display: grid;
    grid-template-columns: 1fr 2fr;
    gap: 30px;

    @media (max-width: 1440px) {
      gap: 20px;
    }

    @media (max-width: 1280px) {
      grid-template-columns: 1fr;
      gap: 40px;
    }

    @media (max-width: 425px) {
      gap: 18px;
    }

    &__form {
      max-width: 520px;
      width: 100%;
      margin: 0 auto;

      @media (max-width: 768px) {
        max-width: 425px;
      }
    }

    &__features {
      display: grid;
      grid-template-columns: repeat(3, 1fr);

      column-gap: 30px;

      @media (max-width: 1440px) {
        gap: 20px;
      }

      @media (max-width: 1280px) {
        max-width: max-content;
        margin: 0 auto;
      }

      @media (max-width: 768px) {
        grid-template-columns: 1fr;
        grid-template-rows: repeat(3, 1fr);
        width: 100%;
        max-width: 330px;
      }

      @media (max-width: 576px) {
        gap: 10px;
      }

      .features-item {
        @extend %shadow-block;
        display: flex;
        flex: 1 1 0;
        max-width: 330px;
        flex-direction: column;
        gap: 30px;
        padding: 30px 20px 45px;

        @media (max-width: 1280px) {
          max-width: 260px;
          gap: 25px;
        }

        @media (max-width: 992px) {
          padding: 24px 10px 45px;
        }

        @media (max-width: 768px) {
          flex-direction: row;
          align-items: center;
          padding: 24px;
          width: 100%;
          max-width: 100%;
        }

        @media (max-width: 576px) {
          padding: 24px 14px;
          padding-left: 7%;
          gap: 18px;
        }

        &__svg {
          @media (max-width: 768px) {
            flex-shrink: 0;
          }
          svg {
            display: block;
            margin: 0 auto;

            @media (max-width: 1440px) {
              width: 80px;
            }

            @media (max-width: 1280px) {
              width: 70px;
            }

            @media (max-width: 768px) {
              width: 50px;
            }
          }
        }

        &__line {
          @media (max-width: 768px) {
            height: 100%;
          }
          .gorizontal-line {
            @media (max-width: 992px) {
              width: 90%;
              margin: 0 auto;
            }

            @media (max-width: 768px) {
              height: 100%;
              width: 2px;
            }
          }
        }

        &__content {
          h4 {
            font-family: "Roboto";
            font-style: normal;
            font-weight: 700;
            font-size: 22px;
            line-height: 27px;
            text-align: center;
            color: $gray;
            margin-bottom: 14px;
            white-space: nowrap;

            br {
              display: none;
            }

            @include fluidFontSize(18, 22, 993, 1920);

            @media (max-width: 768px) {
              font-size: 18px;
              line-height: 20px;
              margin-bottom: 8px;
              text-align: left;

              br {
                display: block;
              }
            }
          }

          p {
            font-family: "Roboto";
            font-style: normal;
            font-weight: 400;
            font-size: 20px;
            line-height: 24px;
            text-align: center;
            color: $gray;
            white-space: nowrap;

            @include fluidFontSize(16, 20, 993, 1920);

            @media (max-width: 768px) {
              font-size: 14px;
              text-align: left;

              line-height: 17px;
            }
          }
        }
      }
    }
  }
}
