@keyframes sheen {
  100% {
    transform: translateX(100%);
  }
}
#delivery .delivery-wrapper__features .features-item {
  background: #ffffff;
  border: 3px solid #ececec;
  box-shadow: 15px 15px 40px rgba(0, 0, 0, 0.1);
  border-radius: 20px;
}

#delivery {
  padding-bottom: 100px;
}
@media (max-width: 992px) {
  #delivery {
    padding-bottom: 80px;
  }
}
@media (max-width: 768px) {
  #delivery {
    padding-bottom: 60px;
  }
}

#delivery .delivery-wrapper {
  display: grid;
  grid-template-columns: 1fr 2fr;
  gap: 30px;
}
@media (max-width: 1440px) {
  #delivery .delivery-wrapper {
    gap: 20px;
  }
}
@media (max-width: 1280px) {
  #delivery .delivery-wrapper {
    grid-template-columns: 1fr;
    gap: 40px;
  }
}
@media (max-width: 425px) {
  #delivery .delivery-wrapper {
    gap: 18px;
  }
}
#delivery .delivery-wrapper__form {
  max-width: 520px;
  width: 100%;
  margin: 0 auto;
}
@media (max-width: 768px) {
  #delivery .delivery-wrapper__form {
    max-width: 425px;
  }
}
#delivery .delivery-wrapper__features {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  column-gap: 30px;
}
@media (max-width: 1440px) {
  #delivery .delivery-wrapper__features {
    gap: 20px;
  }
}
@media (max-width: 1280px) {
  #delivery .delivery-wrapper__features {
    max-width: max-content;
    margin: 0 auto;
  }
}
@media (max-width: 768px) {
  #delivery .delivery-wrapper__features {
    grid-template-columns: 1fr;
    grid-template-rows: repeat(3, 1fr);
    width: 100%;
    max-width: 330px;
  }
}
@media (max-width: 576px) {
  #delivery .delivery-wrapper__features {
    gap: 10px;
  }
}
#delivery .delivery-wrapper__features .features-item {
  display: flex;
  flex: 1 1 0;
  max-width: 330px;
  flex-direction: column;
  gap: 30px;
  padding: 30px 20px 45px;
}
@media (max-width: 1280px) {
  #delivery .delivery-wrapper__features .features-item {
    max-width: 260px;
    gap: 25px;
  }
}
@media (max-width: 992px) {
  #delivery .delivery-wrapper__features .features-item {
    padding: 24px 10px 45px;
  }
}
@media (max-width: 768px) {
  #delivery .delivery-wrapper__features .features-item {
    flex-direction: row;
    align-items: center;
    padding: 24px;
    width: 100%;
    max-width: 100%;
  }
}
@media (max-width: 576px) {
  #delivery .delivery-wrapper__features .features-item {
    padding: 24px 14px;
    padding-left: 7%;
    gap: 18px;
  }
}
@media (max-width: 768px) {
  #delivery .delivery-wrapper__features .features-item__svg {
    flex-shrink: 0;
  }
}
#delivery .delivery-wrapper__features .features-item__svg svg {
  display: block;
  margin: 0 auto;
}
@media (max-width: 1440px) {
  #delivery .delivery-wrapper__features .features-item__svg svg {
    width: 80px;
  }
}
@media (max-width: 1280px) {
  #delivery .delivery-wrapper__features .features-item__svg svg {
    width: 70px;
  }
}
@media (max-width: 768px) {
  #delivery .delivery-wrapper__features .features-item__svg svg {
    width: 50px;
  }
}
@media (max-width: 768px) {
  #delivery .delivery-wrapper__features .features-item__line {
    height: 100%;
  }
}
@media (max-width: 992px) {
  #delivery .delivery-wrapper__features .features-item__line .gorizontal-line {
    width: 90%;
    margin: 0 auto;
  }
}
@media (max-width: 768px) {
  #delivery .delivery-wrapper__features .features-item__line .gorizontal-line {
    height: 100%;
    width: 2px;
  }
}
#delivery .delivery-wrapper__features .features-item__content h4 {
  font-family: "Roboto";
  font-style: normal;
  font-weight: 700;
  font-size: 22px;
  line-height: 27px;
  text-align: center;
  color: #2b2b2b;
  margin-bottom: 14px;
  white-space: nowrap;
  font-size: calc(0.0043149946 * 100vw + 13.715210356px);
}
#delivery .delivery-wrapper__features .features-item__content h4 br {
  display: none;
}
@media (min-width: 1921px) {
  #delivery .delivery-wrapper__features .features-item__content h4 {
    font-size: 22px;
  }
}
@media (max-width: 993px) {
  #delivery .delivery-wrapper__features .features-item__content h4 {
    font-size: 18px;
  }
}
@media (max-width: 768px) {
  #delivery .delivery-wrapper__features .features-item__content h4 {
    font-size: 18px;
    line-height: 20px;
    margin-bottom: 8px;
    text-align: left;
  }
  #delivery .delivery-wrapper__features .features-item__content h4 br {
    display: block;
  }
}
#delivery .delivery-wrapper__features .features-item__content p {
  font-family: "Roboto";
  font-style: normal;
  font-weight: 400;
  font-size: 20px;
  line-height: 24px;
  text-align: center;
  color: #2b2b2b;
  white-space: nowrap;
  font-size: calc(0.0043149946 * 100vw + 11.715210356px);
}
@media (min-width: 1921px) {
  #delivery .delivery-wrapper__features .features-item__content p {
    font-size: 20px;
  }
}
@media (max-width: 993px) {
  #delivery .delivery-wrapper__features .features-item__content p {
    font-size: 16px;
  }
}
@media (max-width: 768px) {
  #delivery .delivery-wrapper__features .features-item__content p {
    font-size: 14px;
    text-align: left;
    line-height: 17px;
  }
}